<template>
    <div>
        <div class="kd-page-title">进行支付</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap">
            <div class="ewmText" v-if="successlist && successlist.length > 0">
                请扫描下方支付二维码，输入处罚决定书编号：
                <span v-for="(item,index) in successlist" :key="index">{{ item }} </span>
            </div>
            <div class="ewmText" v-if="failedlist && failedlist.length > 0">
                同步失败的决定书，请至罚款缴纳重新同步：
                <span v-for="(item,index) in failedlist" :key="index">{{ item }} </span>
            </div>
            <div class="erm">
                <div>
                    <span>支付宝缴费</span>
                    <img alt="" :src="zhifubao">
                </div>
                <div>
                    <span>微信缴费</span>
                    <img alt="" :src="weixin">
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="()=>{this.changeStep('down');}"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import zhifubao2 from '@/assets/qrcode/zhifubao2.jpg';
import weixin from '@/assets/qrcode/weixin.jpg';
import audioCar from '@/assets/media/trafic/107.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'TrafficPayQr',
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } },
        successlist: { type: Array, default: () => [] },
        failedlist: { type: Array, default: () => [] }
    },
    data () {
        return {
            zhifubao: zhifubao2,
            weixin: weixin,
            intervalId: null
        };
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.changeVisible({ visibleJurisdiction: false });
    },
    beforeDestroy () {
        this.changeVisible({ visibleJurisdiction: true });
        this.audioCar.destroy();
        clearInterval(this.intervalId);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'changeVisible']),
        async nextStep () {
            await this.nextFunction();
        }

    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .warnText {
            text-align: center;
            color: red;
        }

        .ewmText {
            font-size: 19.2px;
            text-align: center;
            span {
                font-size: 24px;
            }
        }

        .erm {
            display: flex;
            justify-content: space-around;
            align-items: center;

            > div {
                display: flex;
                flex-direction: column;
                justify-content: space-around;

                span {
                    text-align: center;
                    padding-bottom: 20px;
                }

                img {
                    height: 260px;
                }
            }
        }

    }
</style>
