<template>
    <div>
        <div class="kd-page-title">
            进行支付
        </div>
        <div class="kd-page-content-wrapper kd-page-padding payResult">
            <p>请使用支付宝扫码支付：<span>{{ illegalityPayUrl.fkzje }}</span>元</p>
            <qriously
                class="payValue"
                :value="illegalityPayUrl.smURL"
                :size="260"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
        <alert-msg
            v-if="alertOn"
            :alertSure="sure"
            :alertCancel="cancel"
            tip-text="确定已支付，并提交业务？"
        />
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import alertMsg from './alert-msg';
import audioCar from '@/assets/media/trafic/10.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'PayQuery',
    props: ['handleComplete', 'illegalityPayUrl'],
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    data () {
        return {
            alertOn: false
        };
    },
    components: { alertMsg },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        nextStep () {
            this.alertOn = true;
        },
        async sure () {
            if (this.handleComplete) {
                await this.handleComplete();
            } else {
                this.changeStep('down');
            }
        },
        cancel () {
            this.alertOn = false;
        },
        preStep () { // 上一步
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .payResult {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 26px;

        p {
            margin-bottom: 20px;

            span {
                font-size: 48px;
            }
        }

        .payValue {
            background: #fff;
            margin-bottom: 20px;
        }
    }
</style>
