<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process v-if="currentStep === steps.findIndex(step=>step.key === 'process')" :handle-business="handleBusiness"/>
        <fine-type v-if="currentStep === steps.findIndex(step=>step.key === 'fineType')" :nextFunction="chooseFineType"/>
        <fine-record
            v-if="currentStep === steps.findIndex(step=>step.key === 'fineRecord')"
            :isCheck="false"
            :payType="illegalityPayType"
            :searchFunction="searchFunction"
            :nextFunction="startPay"
            :getSelectionList="getSelectionList"
            :list="illegalityPayList"
            :chooseTypeNumber="chooseTypeNumber"
        />

        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'synjdsbh')" :handle-business="synjdsbh" waitText="正在同步决定书，请稍后......"/>
        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'synjdsbhmaanshan')" :handle-business="synjdsbhmanshan" waitText="正在同步决定书，请稍后......"/>
        <process :type="4" v-if="currentStep === steps.findIndex(step=>step.key === 'generatePayUrl')" :handle-business="generatePayUrl" waitText="正在生成支付二维码，请稍后......"/>

        <pay-query v-if="currentStep === steps.findIndex(step=>step.key === 'payQuery')" :illegalityPayUrl="illegalityPayUrl"/>
        <pay-qr-bb
            v-if="currentStep === steps.findIndex(step=>step.key === 'payQr')"
            :nextFunction="payResultCheck"
            :successlist="successArr"
            :failedlist="failedArr"
        />
        <pay-qr-maanshan
            v-if="currentStep === steps.findIndex(step=>step.key === 'payQrMaanshan')"
            :nextFunction="payResultCheck"
            :successlist="successArr"
            :failedlist="failedArr"
        />
        <pay-qr-multiple v-if="currentStep === steps.findIndex(step=>step.key === 'payQrMultiple')" :pay-data="illegalitySelectList"/>

        <process :type="5" v-if="currentStep === steps.findIndex(step=>step.key === 'completeProcess')" :handle-business="handleComplete"/>

        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="illegalityPayResult"
            foot-texts="温馨提示：违法行为将在24小时之内消除，如消除失败，系统将于7个工作日之内自动退款"
            :mp3="audioCom"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import fineType from './components/fine-type';
import fineRecord from './components/fine-record';
import complete from '@/components/complete';
import payQuery from '@/components/pay-query';
import payQrBb from './../../components/pay-qr-bb';
import payQrMaanshan from './../../components/pay-qr-maanshan';
import payQrMultiple from '../../components/pay-qr-multiple';
import process from '@/components/process';
import { illegalityPayFaceSuccess, illegalityPayCheck, illegalityPayQueryList, illegalityGetQR, synchronizationData, illegalityPayResultCheck } from '@/service/traffic/traffic';
import { ILLEAGALITY_BUS_CODE, LOCATION_ID } from '@/constant/constant';
import audioCom from '@/assets/media/trafic/11.mp3';
import { ILLEGALITY_PAYMENT_ANHUI, ILLEGALITY_PAYMENT_MAANSHAN, ILLEGALITY_PAYMENT_SUZHOU, ILLEGALITY_PAYMENT_YINCHUAN } from '@/constant/stepsOption';
import { checkIsAnHuiPay, illegalTypeChoose } from '@/utils/regional';

export default {
    name: 'IllegalityPayIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        fineType,
        fineRecord,
        complete,
        payQuery,
        payQrBb,
        payQrMultiple,
        process,
        payQrMaanshan
    },
    data () {
        return {
            audioCom: audioCom,
            illegalityPayType: 0,
            illegalityPayList: [],
            illegalitySelectList: [],
            illegalityPayUrl: '',
            successArr: [],
            failedArr: [],
            illegalityPayResult: {},
            inputData: '',
            chooseTypeNumber: [true, true]
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async handleBusiness () {
            if (illegalTypeChoose(this.CONSTANT.location)) {
                this.illegalityPayType = 0;
                this.chooseTypeNumber = [false, true];
            }
            const res = await illegalityPayFaceSuccess({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleComplete () {
            const res = await illegalityPayCheck({
                payStatus: 1,
                penaltyDetailIds: this.illegalitySelectList.map((item) => {
                    return item.penaltyDetailId;
                })
            });
            if (res && res.code === '0') {
                this.illegalityPayResult = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        chooseFineType (state) {
            this.illegalityPayList = [];
            this.illegalityPayType = state;
            this.changeStep('down');
        },
        async searchFunction (data, inputData) {
            this.inputData = inputData;
            const res = await illegalityPayQueryList(data);
            if (res && res.code === '0') {
                this.$message.success('查询成功！');
                const result = res.result;

                result.forEach((item) => {
                    if (!item.lateFee) {
                        item.lateFee = 0;
                    }
                    if (!item.payAmount) {
                        item.payAmount = 0;
                    }
                });
                this.illegalityPayList = result;

                if (res.result.length === 0) {
                    this.changeTipPage({ message: '未查询到待缴费记录！', type: 1, enableBack: true });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        getSelectionList (selections) {
            this.illegalitySelectList = selections;
        },
        async startPay () {
            this.changeStep('down');
        },
        async generatePayUrl () {
            const res = await illegalityGetQR({
                busCode: ILLEAGALITY_BUS_CODE[this.businessTypeCode],
                jdsbhJkbhs: this.illegalitySelectList.map((item) => {
                    return item.decisionNumber;
                })
            });
            if (res && res.code === '0') {
                this.illegalityPayUrl = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async payResultCheck () {
            const success = [];

            const penaltyDetailIdList = [];

            this.successArr.forEach((item) => {
                const match = this.illegalitySelectList.find((i) => { return i.decisionNumber === item; });
                if (match) {
                    penaltyDetailIdList.push(match.penaltyDetailId);
                }
            });

            if (penaltyDetailIdList.length > 0) {
                const res = await illegalityPayResultCheck({ penaltyDetailIds: penaltyDetailIdList });
                if (res && res.code === '0') {
                    res.result.forEach((item) => {
                        if (item.payStatus === 2) { // 0-待支付 2-支付完成 3 - 支付失败 4 - 订单不存在
                            success.push(item);
                        }
                    });
                    if (success.length === this.successArr.length) {
                        this.changeStep('down');
                    }
                }
            }
        },
        async synjdsbh () {
            this.successArr = [];
            this.failedArr = [];
            const jdsbh = this.illegalitySelectList.map((item) => {
                return item.decisionNumber;
            });
            const res = await synchronizationData({
                jszh: this.illegalityPayType === 0 ? this.inputData : this.illegalitySelectList[0].jszh,
                jdsbh: jdsbh
            });
            if (res && res.code === '0') {
                res.result.forEach((item) => {
                    if (item.status === 0) {
                        this.successArr.push(item.jdsbh);
                    } else {
                        this.failedArr.push(item.jdsbh);
                    }
                });
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        }
    },
    mounted () {
        if (checkIsAnHuiPay(this.CONSTANT.location)) {
            this.setSteps(ILLEGALITY_PAYMENT_ANHUI);
        } else if (this.CONSTANT.location === LOCATION_ID.maanshan) {
            this.setSteps(ILLEGALITY_PAYMENT_MAANSHAN);
        } else if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) { // 银川多个支付二维码
            this.setSteps(ILLEGALITY_PAYMENT_YINCHUAN);
        } else {
            this.setSteps(ILLEGALITY_PAYMENT_SUZHOU);
        }
    }
};

</script>
