<template>
	<div>
		<div class="kd-page-title">进行支付</div>
		<div class="kd-page-content-wrapper kd-page-padding wrap-info">
			<p class="tipWarn">请及时通过支付宝/微信扫码支付，逾期将产生滞纳金。</p>
			<div class="preview" v-loading="loading">
				<div v-for="(item,index) in payUrls" :key="index">
					<qriously
							:key="index"
							v-if="(index + 1) === step"
							class="payValue"
							:value="item.img"
							:size="300"
					/>
					<p>当前决定书编号：{{item.jdsbh}}</p>
				</div>
			</div>
			<div class="page">
				<div :class="disablePre() ? 'grey' : ''" @click="pre">上一页</div>
				<div>第{{ step }}页/共{{ payUrls.length }}页</div>
				<div :class="disableNext() ? 'grey' : ''" @click="next">下一页</div>
			</div>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="nextStep"/>
		</div>
		<alert-msg
				v-if="alertOn"
				:alertSure="sure"
				:alertCancel="cancel"
				tip-text="请确认已支付并提交业务，若未支付完成请至罚款缴纳查询！"
		/>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import alertMsg from '@/components/alert-msg';
import audioCar from '@/assets/media/trafic/107.mp3';
import { playAudio } from '@/utils/media';
import { illegalityElectronicGetPrintInfo } from '@/service/traffic/traffic';

export default {
    name: 'TrafficPayQrMultiple',
    props: {
        payData: Array,
        handleComplete: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        },
        returnPrintInfo: Function
    },
    components: { alertMsg },
    computed: {
        ...mapState('common', {
            printId: 'printId'
        })
    },
    data () {
        return {
            step: 1,
            payUrls: [],
            alertOn: false,
            printInfos: [],
            loading: false
        };
    },
    async mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();

        this.loading = true;
        const res = await illegalityElectronicGetPrintInfo({
            decisionNumbers: this.payData.map((item) => {
                return item.decisionNumber;
            })
        });
        if (res && res.code === '0') {
            res.result.forEach((item) => {
                this.payUrls.push({
                    img: item.payQrCode,
                    jdsbh: item.jdsbh
                });
            });
            this.printInfos = res.result;
            if (this.returnPrintInfo) {
                this.returnPrintInfo(res.result);
            }
        } else if (res) {
            this.changeTipPage({ message: res.message, enableBack: true });
        }
        this.loading = false;
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityResult']),
        disablePre () {
            if (this.step <= 1) {
                return true;
            }
        },
        disableNext () {
            if (this.step >= this.payUrls.length) {
                return true;
            }
        },
        pre () {
            if (this.disablePre()) {
                return false;
            }
            this.step = this.step - 1;
        },
        next () {
            if (this.disableNext()) {
                return false;
            }
            this.step = this.step + 1;
        },
        nextStep () {
            this.alertOn = true;
        },
        async sure () {
            if (this.handleComplete) {
                await this.handleComplete();
            }
        },
        cancel () {
            this.alertOn = false;
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		position: relative;

		.tipWarn {
			text-align: center;
		}

		.preview {
			width: 100%;
			height: calc(100% - 50px);
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;

			.payValue {
				margin: 0 50PX 10px;
				text-align: center;
				background: #ffffff;
			}
		}

		.page {
			width: 100%;
			height: 30px;
			display: flex;
			align-items: center;
			justify-content: space-between;

			.grey {
				color: grey;
			}
		}
	}
</style>
