<template>
    <div>
        <div class="warp">
            <ul class="item">
                <li v-if="chooseTypeNumber[0]" @click="selectType(1)">
                    <span class="kedaIconfont kedaIcon-cadIcon6"></span>
                    <span>决定书编号查询</span>
                </li>
                <li v-if="chooseTypeNumber[1]" @click="selectType(0)">
                    <span class="kedaIconfont kedaIcon-cadIcon8"></span>
                    <span>驾驶证号查询</span>
                </li>
            </ul>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import audioCar from '@/assets/media/trafic/200.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'FineType',
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } },
        chooseTypeNumber: { type: Array, default: () => [true, true] }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityPayType']),
        async selectType (type) { // 选择缴费对象
            await this.nextFunction(type);
        },
        preStep () { // 上一步
            this.changeStep('up');
        }
    }

};
</script>

<style lang="scss" scoped>
    .warp {
        box-sizing: border-box;

        .item {
            width: 44.68vw;
            height: 66.25vh;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            justify-content: center;

            li {
                cursor: pointer;
                box-sizing: border-box;
                padding: 0 3vh;
                list-style: none;
                margin: 1.5vh 0;
                height: 11.13vh;
                line-height: 11.13vh;
                background: linear-gradient(#70A3D8, #3A84BF);
                border: 1PX solid #EDFFFF;/*no*/
                border-radius: 1vh;

                span {
                    color: #ffffff;
                    font-size: 3.5vh !important;
                    background: none;
                }

                span:first-child {
                    border-right: 0.2vh solid #8FC9FF;
                    padding-right: 3vw;
                    background: none;
                }

                span:last-child {
                    padding-left: 2vw;
                    background: none;
                }
            }

            :active {
                background: linear-gradient(#87b8d8, #6598bf);
            }
        }
    }
</style>
