<template>
    <div class="bgWrap">
        <div class="msgWrap">
            <img :src="imgObj">
            <p v-html="tipText"></p>
            <div v-if="!disableBut">
                <kd-button v-if="!disableButSure" :text="btnTextLeft" :button-handle="sure"/>
                <kd-button v-if="!disableButCancel" :text="btnTextRight" :button-handle="cancel"/>
            </div>
        </div>
    </div>
</template>

<script>
import waitDone from '@/assets/images/wait_done.png';

export default {
    name: 'AlertMsg',
    props: {
        tipText: String,
        btnTextLeft: { type: String, default: '确定' },
        btnTextRight: { type: String, default: '取消' },
        complete: { default: false },
        disableBut: { default: false },
        disableButCancel: { default: false },
        disableButSure: { default: false },
        alertSure: { type: Function, default: function () {} },
        alertCancel: { type: Function, default: function () {} },
        imgObj: { default: waitDone }
    },
    methods: {
        async sure () {
            if (this.alertSure) {
                await this.alertSure();
            }
        },
        async cancel () {
            if (this.alertCancel) {
                await this.alertCancel();
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .bgWrap {
        width: 100vw;
        height: 100vh;
        background: rgba(0, 0, 0, 0.7);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 99999;
        display: flex;
        justify-content: center;
        align-items: center;

        .msgWrap {
            width: 80%;
            height: 576px;
            background: rgba(255, 255, 255, 1);
            border-radius: 6px;
            border: 1px solid;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;

            p {
                font-size: 32px;
                color: #3C4B78;
                margin: 30px 100px;
                line-height: 45px;
            }

            > div button {
                margin: 0 2vw;
            }

            > div button:nth-child(1) {
                background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
            }
        }
    }
</style>
